<template>
<div class="Url-itemPage">
    <i-modal v-model="addUserDictWindowShow" title="" :closable="false" :mask-closable="false" :scrollable="true">

        <div class="Url-itemPage-form">

            <i-form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="100">

                <i-formItem label="" prop="word">
                    <i-input class="management-newProject-name-input" @on-change="searchUserWordStatus(formCustom.word)" type="text" v-model="formCustom.word" placeholder=""></i-input>
                </i-formItem>

                <i-formItem class="status" label=":">
                    <i-row class="status-check">
                        <span class="xyz">{{status}}</span>
                    </i-row>
                </i-formItem>

                <div class="Url-itemPage-form-category">
                    <i-formItem label="" prop="categorySelect">
                        <div class="Url-itemPage-form-category-inside">
                            <i-select class="Url-itemPage-form-category-select" multiple v-model="formCustom.categorySelect">
                                <i-option v-for="item in rawCategories" :value="item" :key="item">{{ item }}</i-option>
                            </i-select>
                        </div>
                    </i-formItem>
                </div>
            </i-form>

        </div>
        <div slot="footer">
            <i-button type="default" size="large" @click="modalCancel('formCustom')"></i-button>
            <i-button :disabled="submitAllow" type="primary" size="large" @click="modalOk('formCustom')"></i-button>
        </div>
    </i-modal>
</div>
</template>

<script>
import {
    mapState,
    mapMutations
} from 'vuex'
export default {
    name: 'adduserDictWindow',
    data() {
        const validateUserWord = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));

            } else {
                callback();
            }
        };
        const validateCategory = (rule, value, callback) => {
            if (value.length === 0) {
                console.log('category empty')
                callback(new Error(''));
            } else {

                callback();
            }
        };
        return {
            submitAllow: true,
            status: '',
            formCustom: {
                word: '',
                categorySelect: [],
            },
            ruleCustom: {
                word: [{
                    validator: validateUserWord,
                    trigger: 'blur'
                }],
                categorySelect: [{
                    validator: validateCategory,
                    trigger: 'blur,change'
                }]
            },
        }
    },
    props: ['addUserDictWindowShow', 'rawCategories'],
    computed: {
        ...mapState(['baseurl', 'currentComponent']),
    },
    beforeDestroy() {},
    mounted() {},
    methods: {
        ...mapMutations(['changeUrlItemWindowShow']),
        searchUserWordStatus: function (wordPart) {

            //          
            console.log('wordPart', wordPart)
            if (wordPart.replace(/ /g, '')) { //  
                this.status = ' ...'
                this.checkIfExist(wordPart)
            }

        },
        checkIfExist: function (target) {
            let self = this
            let Params = {
                'keyword': target,
                'fullMatch': true
            } //  
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'StopDict/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    // console.log('StopDict ...',res)
                    //  
                    if (res.data.count > 0) {
                        // 
                        // self.formCustom.Items[xindex].value.status = ' ' + '(' + res.data.content[0].modifiedTime + ')'
                        //self.$set(self.formCustom.Items[xindex].value, 'status', ' ' + '(' + res.data.content[0].modifiedTime + ')')
                        self.submitAllow = true
                        self.status = ' '
                    } else {
                        //  StopDict   InvalidDict 
                        // console.log('not StopDict')
                        let Params = {
                            'keyword': target,
                            'fullMatch': true
                        }
                        self.axios({
                                method: 'get',
                                url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                                params: Params
                            })
                            .then(res => {
                                // console.log('invalidDict ...',res)
                                if (res.data.count > 0) {
                                    //  
                                    //self.formCustom.Items[xindex].value.status = ' ' + '(' + res.data.content[0].modifiedTime + ')'
                                    //self.$set(self.formCustom.Items[xindex].value, 'status', ' ' + '(' + res.data.content[0].modifiedTime + ')')
                                    self.submitAllow = true
                                    self.status = ' '
                                } else {
                                    //  ,   
                                    // console.log('not invalidDict')
                                    let Params = {
                                        'keyword': target,
                                        'fullMatch': true
                                    }
                                    self.axios({
                                            method: 'get',
                                            url: self.baseurl + 'UserDict/' + self.currentComponent,
                                            params: Params
                                        })
                                        .then(res => {
                                            // console.log('userDict ...',res)
                                            if (res.data.count > 0) {
                                                //  
                                                //self.formCustom.Items[xindex].value.status = ' ' + '(' + res.data.content[0].modifiedTime + ')'
                                                //self.$set(self.formCustom.Items[xindex].value, 'status', ' ' + '(' + res.data.content[0].modifiedTime + ')')
                                                self.submitAllow = true
                                                self.status = ' '
                                            } else {
                                                //    
                                                // console.log('not userDict')
                                                self.status = ' '
                                                self.submitAllow = false
                                            }
                                        })
                                        .catch(err => {
                                            console.log(err)
                                        })
                                }
                            })
                            .catch(err => {
                                console.log(err)
                            })
                    }
                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })

        },
        modalOk: function (name) {
            let self = this
            // check  
            self.$refs[name].validate((valid) => {
                if (valid) {
                    console.log('')
                    // 
                    let userDictInfo = {}
                    userDictInfo['word'] = self.formCustom.word
                    userDictInfo['categories'] = self.formCustom.categorySelect
                    userDictInfo['operator'] = localStorage.getItem('kwmUser')
                    // console.log(userDictInfo)

                    // 
                    self.axios({
                            method: 'post',
                            url: self.baseurl + 'UserDict/' + self.currentComponent,
                            data: [userDictInfo]
                        })
                        .then(() => {
                            //console.log(res)
                            self.$Message.success('!');
                            self.$refs[name].resetFields()
                            this.$emit('update:addUserDictWindowShow', false)
                        })
                        .catch(err => {
                            console.log(err)
                            //self.$Message.error('!');
                            this.$emit('update:addUserDictWindowShow', false)
                            self.$Message.error({
                                content: JSON.stringify(err.response.data.detail),
                                duration: 0,
                                closable: true
                            });
                        })

                } else {
                    self.$Message.error('');
                }
            })
        },
        modalCancel: function (name) {
            this.$refs[name].resetFields()
            this.$emit('update:addUserDictWindowShow', false)

        }
    }
}
</script>

<style scoped>
.xyz {
    background-color: yellow
}
</style>
