<template>
<div class="Article-see-detail">
    <i-modal v-model="showSeeDetail" :title="':  ' + xcurrentPage +  '' + (xindex + 1 )  + ''" width="1050px" :closable="false" :mask-closable="false" :scrollable="true">
        <div class="Article-see-detail-content-total" v-if="showSeeDetail && articleDetail !==[]">
            <div class="Article-see-detail-content-total1">
                <div class="article-detail-content-p1">
                    {{seeBody}}
                </div>
                <div class="article-detail-content-p2">
                    <li class="liLine header">
                        <div class="p1"></div>
                        <div class="p2"></div>
                    </li>
                    <li class="liLine main" v-for="(item,index) in this.seeSplitWords" :key="index">
                        <div class="p1">{{item}}</div>
                        <div class="p2">
                            <i-button class="Stop" style="color: #54616f;background-color: #e4f5f7;border: 1px solid green; margin-top: 2px; margin-bottom: 2px" size="small" @click="setStopWord(item,index)"></i-button>
                            <i-button class="Invalid" style="color: #7b5ae4;background-color: #e4f5f7;border: 1px solid green; margin-top: 2px; margin-bottom: 2px" size="small" @click="setInvalidWord(item,index)"></i-button>
                        </div>
                    </li>
                </div>
            </div>
            <div class="Article-see-detail-content-total2">
                <i-button class="xbutton setInvalid" :disabled="this.seeStatus === ''" type="warning" size="default" @click="setUrlInvalid('invalid')" icon="md-warning"></i-button>
                <i-button class="xbutton setValid" :disabled="this.seeStatus !== '' " type="warning" size="default" @click="setUrlInvalid('valid')" icon="md-medkit"></i-button>
                <i-button class="xbutton addUserDict" type="success" size="default" @click="add2UserDict()"></i-button>
            </div>
        </div>
        <div slot="footer">
            <i-button type="primary" :disabled="previousDisabled" size="large" shape="circle" @click="PreviousOne()"></i-button>
            <i-button type="primary" :disabled="nextDisabled" size="large" shape="circle" @click="nextOne()"></i-button>
            <i-button type="default" size="large" @click="modalCancel()"></i-button>
        </div>
    </i-modal>
    <adduserDictWindow :rawCategories="rawCategories" :addUserDictWindowShow.sync="addUserDictWindowShow"></adduserDictWindow>
</div>
</template>

<script>
import {
    mapState
} from 'vuex'
import adduserDictWindow from '@/components/Articles/adduserDictWindow.vue'
export default {
    name: 'seeDetails',
    data() {
        return {
            xindex: JSON.parse(JSON.stringify(this.currentIndex)),
            xcurrentPage: JSON.parse(JSON.stringify(this.currentPage)),
            xpageSize: JSON.parse(JSON.stringify(this.pageSize)),
            xtotalItems: 0,
            articleDetail: [],
            nextDisabled: false,
            previousDisabled: false,
            addUserDictWindowShow: false,
        }
    },
    components: {
        adduserDictWindow
    },
    props: ['showSeeDetail', 'currentIndex', 'currentPage', 'pageSize', 'rawCategories'],
    mounted() {
        // console.log('mounted .....')
        let self = this
        if (self.showSeeDetail) {
            let Params = {
                'currentPage': self.xcurrentPage,
                'pageSize': self.xpageSize
            }
            self.fetchItem(Params)

        }
    },
    computed: {
        ...mapState(['baseurl', 'currentComponent']),
        xtotalPages: function () {
            if (this.articleDetail.length !== 0) {
                return Math.ceil(this.xtotalItems / this.xpageSize)
            } else {
                return 0
            }
        },
        seeStatus: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex].status
            } else {
                return ''
            }
        },
        seeBody: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex].body
            } else {
                return ''
            }
        },
        seeSplitWords: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return Array.from(new Set(this.articleDetail[this.xindex].splitWords))
            } else {
                return []
            }
        },
        seeUid: function () {
            if (this.articleDetail.length !== 0 && this.xindex < this.currentPagetotalCount && this.xindex >= 0) {
                return this.articleDetail[this.xindex]['_id']['$oid']
            } else {
                return ''
            }
        },
        currentPagetotalCount: function () {
            return this.articleDetail.length
        }

    },

    methods: {
        fetchItem: function (Params) {
            let self = this
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'Articles/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    // console.log(res)
                    if (res.data.count !== '') {
                        self.xtotalItems = res.data.count
                    }
                    self.articleDetail = res.data.content
                    //      
                    if (self.xindex === self.currentPagetotalCount - 1 && self.xcurrentPage === self.xtotalPages) {
                        self.nextDisabled = true
                    }
                    if (self.xindex === 0 && self.xcurrentPage === 1) {
                        self.previousDisabled = true
                    }

                })
                .catch(err => {
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })
        },
        add2UserDict: function () {
            let self = this
            //   
            //  formCustom
            self.addUserDictWindowShow = true

        },
        setStopWord: function (word, index) {
            console.log(word, index)
            let self = this
            //           
            //    

            // 0-   
            // let tempLine = {
            //     'id': self.selectedItemList[element]['_id']['$oid'],
            //     'status': self.selectedItemList[element].status,
            //     'word': self.selectedItemList[element].word,
            //     'operator': localStorage.getItem('kwmUser'),
            //     'source': ''
            // }
            //console.log('tempLine', tempLine)

            // 2 -  
            let Params = {
                'keyword': word.trim(),
                'fullMatch': true
            }
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    //console.log(res)
                    if (res.data.count > 0) {
                        //   
                        console.log(word + '  ')
                        console.log('!')

                        //  
                        self
                            .axios({
                                method: "delete",
                                url: self.baseurl + "InvalidDict/" + self.currentComponent,
                                data: [word],
                            })
                            .then(() => {
                                console.log(', ')

                                //  
                                let xdata = [{
                                    'word': word,
                                    'operator': localStorage.getItem('kwmUser'),
                                    'source': '',
                                    'exStatus': ''
                                }]
                                self.axios({
                                        method: 'post',
                                        url: self.baseurl + 'StopDict/' + self.currentComponent,
                                        data: xdata
                                    })
                                    .then(() => {
                                        console.log(' !')

                                        //     
                                        // 2-    
                                        self.deleteFromSplitWords(index)

                                    })
                                    .catch(err => {
                                        //console.log(err)
                                        console.log(' !')
                                        self.$Message.error(err.response.data.detail);
                                        console.log('c' + err.response.data.detail + 'c')
                                        if (err.response.data.detail.indexOf('') !== -1) {
                                            console.log('chongu')
                                            // 2-    
                                            self.deleteFromSplitWords(index)
                                        }

                                    })

                            })
                            .catch((err) => {
                                console.log(err);
                                self.$Message.error('' + word + '!');
                            });
                    } else {
                        console.log(' ')

                        //    
                        let Params = {
                            'keyword': word,
                            'fullMatch': true
                        }
                        self.axios({
                                method: 'get',
                                url: self.baseurl + 'UserDict/' + self.currentComponent,
                                params: Params
                            })
                            .then(res => {
                                if (res.data.count > 0) {
                                    //  
                                    self
                                        .axios({
                                            method: "delete",
                                            url: self.baseurl + "UserDict/" + self.currentComponent,
                                            data: [{
                                                'word': word
                                            }],
                                        })
                                        .then(() => {
                                            console.log(', ')

                                            //  
                                            let xdata = [{
                                                'word': word,
                                                'operator': localStorage.getItem('kwmUser'),
                                                'source': '',
                                                'exStatus': ''
                                            }]
                                            self.axios({
                                                    method: 'post',
                                                    url: self.baseurl + 'StopDict/' + self.currentComponent,
                                                    data: xdata
                                                })
                                                .then(() => {
                                                    console.log(' !')

                                                    // 2-    
                                                    self.deleteFromSplitWords(index)
                                                })
                                                .catch(err => {
                                                    //console.log(err)
                                                    self.$Message.error(err.response.data.detail);
                                                    if (err.response.data.detail.indexOf('') !== -1) {
                                                        console.log('chongu')
                                                        // 2-    
                                                        self.deleteFromSplitWords(index)
                                                    }
                                                })

                                        })
                                        .catch((err) => {
                                            console.log(err);
                                            self.$Message.error("");
                                        });
                                } else {
                                    // 
                                    //  
                                    //  
                                    let xdata = [{
                                        'word': word,
                                        'operator': localStorage.getItem('kwmUser'),
                                        'source': '',
                                        'exStatus': ''
                                    }]
                                    self.axios({
                                            method: 'post',
                                            url: self.baseurl + 'StopDict/' + self.currentComponent,
                                            data: xdata
                                        })
                                        .then(() => {
                                            console.log(' !')

                                            // 2-    
                                            self.deleteFromSplitWords(index)
                                        })
                                        .catch(err => {
                                            //console.log(err)
                                            self.$Message.error(err.response.data.detail);
                                            if (err.response.data.detail.indexOf('') !== -1) {
                                                console.log('chongu')
                                                // 2-    
                                                self.deleteFromSplitWords(index)
                                            }
                                        })

                                }
                            })
                            .catch(err => {
                                console.log(err)
                            })
                    }
                })
                .catch(err => {
                    console.log(err)
                })

        },
        deleteFromSplitWords: function (index) {
            let self = this
            let temp = JSON.parse(JSON.stringify(self.seeSplitWords))
            temp.splice(index, 1)
            let myData = {
                'splitWords': temp,
            }
            let Params = {
                'currentPage': self.xcurrentPage,
                'pageSize': self.xpageSize
            }
            console.log('uid', self.seeUid)
            //  Article  article  
            self.axios({
                    method: 'patch',
                    url: self.baseurl + 'Articles/' + self.currentComponent + '/' + self.seeUid,
                    data: myData,
                    params: Params
                })
                .then(res => {
                    // console.log('yyy',res)
                    //self.seeSplitWords.splice(index,1)
                    if (res.data.count !== '') {
                        self.xtotalItems = res.data.count
                    }
                    self.articleDetail = res.data.content
                    self.$Message.success('!');
                })
                .catch(err => {
                    //console.log(err)
                    self.$Message.error({
                        content: JSON.stringify(err.response.data.detail),
                        duration: 0,
                        closable: true
                    });

                })
        },
        setInvalidWord: function (word, index) {
            let self = this
            //           
            //    

            // 1 -  
            let Params = {
                'keyword': word,
                'fullMatch': true
            }
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'StopDict/' + self.currentComponent,
                    params: Params
                })
                .then(res => {
                    //console.log(res)
                    if (res.data.count > 0) {
                        //   
                        console.log(word + '  ')

                        //  
                        self
                            .axios({
                                method: "delete",
                                url: self.baseurl + "StopDict/" + self.currentComponent,
                                data: [word],
                            })
                            .then(() => {
                                console.log(', ')

                                //  
                                let xdata = [{
                                    'word': word,
                                    'operator': localStorage.getItem('kwmUser'),
                                    'source': '',
                                    'exStatus': ''
                                }]
                                self.axios({
                                        method: 'post',
                                        url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                                        data: xdata
                                    })
                                    .then(() => {
                                        console.log(' !')

                                        self.deleteFromSplitWords(index)
                                    })
                                    .catch(err => {
                                        //console.log(err)
                                        self.$Message.error(err.response.data.detail);
                                        self.deleteFromSplitWords(index)
                                    })

                            })
                            .catch((err) => {
                                console.log(err);
                                self.$Message.error("");
                            });
                    } else {
                        console.log(' ')

                        //    
                        let Params = {
                            'keyword': word,
                            'fullMatch': true
                        }
                        self.axios({
                                method: 'get',
                                url: self.baseurl + 'UserDict/' + self.currentComponent,
                                params: Params
                            })
                            .then(res => {
                                if (res.data.count > 0) {
                                    console.log(' ')
                                    //        //  
                                    self
                                        .axios({
                                            method: "delete",
                                            url: self.baseurl + "UserDict/" + self.currentComponent,
                                            data: [{
                                                'word': word
                                            }],
                                        })
                                        .then(() => {
                                            console.log(', ')

                                            //  
                                            let xdata = [{
                                                'word': word,
                                                'operator': localStorage.getItem('kwmUser'),
                                                'source': '',
                                                'exStatus': ''
                                            }]
                                            self.axios({
                                                    method: 'post',
                                                    url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                                                    data: xdata
                                                })
                                                .then(() => {
                                                    console.log(' !')

                                                    //  
                                                    self.deleteFromSplitWords(index)
                                                })
                                                .catch(err => {
                                                    //console.log(err)
                                                    self.$Message.error(err.response.data.detail);
                                                    self.deleteFromSplitWords(index)
                                                })

                                        })
                                        .catch((err) => {
                                            console.log(err);
                                            self.$Message.error("");
                                        });
                                } else {
                                    // 
                                    //  
                                    //  
                                    console.log(' ')
                                    let xdata = [{
                                        'word': word,
                                        'operator': localStorage.getItem('kwmUser'),
                                        'source': '',
                                        'exStatus': ''
                                    }]
                                    self.axios({
                                            method: 'post',
                                            url: self.baseurl + 'InvalidDict/' + self.currentComponent,
                                            data: xdata
                                        })
                                        .then(() => {
                                            console.log(' !')

                                            //  
                                            self.deleteFromSplitWords(index)
                                        })
                                        .catch(err => {
                                            //console.log(err)
                                            self.$Message.error(err.response.data.detail);
                                            self.deleteFromSplitWords(index)
                                        })
                                }
                            })
                            .catch(err => {
                                console.log(err)
                            })
                    }
                })
                .catch(err => {
                    console.log(err)
                })
        },
        setUrlInvalid: function (flag) {
            //  url  uid     status  
            let self = this
            // 
            let myData = {}
            let operation = ''
            if (flag === 'invalid') {
                myData = {
                    'status': '',
                    'source': ''
                }
                operation = ""
            } else if (flag === 'valid') {
                myData = {
                    'status': '',
                    'source': ''
                }
                operation = ""
            }

            let Params = {
                'currentPage': self.xcurrentPage,
                'pageSize': self.xpageSize
            }
            // console.log(myData)
            self.axios({
                    method: 'patch',
                    url: self.baseurl + 'Articles/' + self.currentComponent + '/' + self.seeUid,
                    data: myData,
                    params: Params
                })
                .then(res => {
                    if (res.data.count !== '') {
                        self.xtotalItems = res.data.count
                    }
                    self.articleDetail = res.data.content
                    self.$Message.success('' + operation + '!');
                })
                .catch(err => {
                    console.log(err)
                    self.$Message.error('' + operation + '!');
                })

        },
        PreviousOne: function () {
            let self = this
            // console.log(self.seeUid)
            self.nextDisabled = false
            self.xindex = self.xindex - 1
            if (self.xindex < 0) {
                // 
                // 1- :
                if (self.xcurrentPage > 1) {
                    // 
                    self.xindex = self.xpageSize - 1 //  
                    self.xcurrentPage = self.xcurrentPage - 1
                    let Params = {
                        'currentPage': self.xcurrentPage,
                        'pageSize': self.xpageSize
                    }
                    self.fetchItem(Params)
                } else {
                    //  
                    self.previousDisabled = true
                }
            } else {
                // self.xindex = self.xindex + 1
                if (self.xindex === 0 && self.xcurrentPage === 1) {
                    self.previousDisabled = true
                }
            }
        },
        nextOne: function () {
            let self = this
            // console.log(self.seeUid)
            self.previousDisabled = false
            self.xindex = self.xindex + 1
            if (self.xindex === self.currentPagetotalCount) {
                //  
                // 1- :
                if (self.xcurrentPage < self.xtotalPages) {
                    // 
                    self.xindex = 0 //
                    self.xcurrentPage = self.xcurrentPage + 1
                    let Params = {
                        'currentPage': self.xcurrentPage,
                        'pageSize': self.xpageSize
                    }
                    self.fetchItem(Params)
                } else {
                    //  
                    self.nextDisabled = true
                }
            } else {
                // self.xindex = self.xindex + 1
                if (self.xindex === self.currentPagetotalCount - 1 && self.xcurrentPage === self.xtotalPages) {
                    self.nextDisabled = true
                }
            }
        },
        modalCancel: function () {
            //this.showSeeUrlConfig = false
            this.$emit('forceRefresh', this.xcurrentPage)
            this.$emit('update:showSeeDetail', false)

        }
    }
}
</script>

